const SearchResults = () => import('~/components/SearchResults')
export default {
  asyncData ({ params }) {
    let query = params.query ? decodeURI(params.query) : ""
    return {
      searchInput: query
    }
  },
  data () {
    return {
      currentPage: 1
    }
  },
  components: { SearchResults }
}
